/*global X2JS, userip*/
import React, { Component } from 'react';
import Field from './components/fields/field';
import formsService from './components/services/forms';
import postInformationService from './components/services/postInformation';
import emitter from './components/config/emitter';
import formSchema from './components/config/form-schema';
import wurl from 'wurl';
import _ from 'lodash';

import './App.css';

class App extends Component {

 form;

 constructor() {
    super();
    this.state = {
      form : {
        firstName : null,
        lastName : null,
        email : null,
        showEmail: true, 
        showBirthDate: false,
        showPhoneNumber: false,
        showProgram: false,
        showLocation: false,
        showEntryTerm: false,
        programs: [{value: 0, label: 'First option'}, {value: 1, label: 'Second option'} ],
        locations: [{value: 0, label: 'First option'}, {value: 1, label: 'Second option'}],
        entries: [{value: 0, label: 'First option'}, {value: 1, label: 'Second option'}]
      },
    };      
    
    let pathUrl = wurl('path').substring(1).split("/");    
    formSchema.setParameterFromUrl(pathUrl);
    emitter.on('valueChanged', this.valueChanged);
  }

  componentDidMount(){             
     formsService.getFormInfo().then((data)=>{
       let form = data[0].data;
       let programs = data[1].data;
       let newValue = this.state.form;
       formSchema.setFormInfo(form);

       newValue.showBirthDate = form.showBirthDate;
       newValue.showEntryTerm = form.showEntryTerm;
       newValue.showPhoneNumber = form.showPhoneNumber;
       newValue.showProgram = form.showProgram;

       this.setState({
         form: newValue
       })
       console.log(data);
     });     
  }

  valueChanged(args){                        
        let schema = formSchema.updateProperty(args.name, args.value);        
   };

  sendLead(e){    
    e.preventDefault();
    let xml = formSchema.getAsXml();        
    let schema = formSchema.getSchema();

    postInformationService.insertRequest('LMSWEB',schema.entry, formSchema.getIdForm(), 2, false, xml, userip, undefined, formSchema.getIsTest()).then(()=>{      
      let form = formSchema.getFormInfo();
      //window.parent.location.href = form.url;
    });
  }

  render() {
    return (
      <div className="App">
        <form>
          <Field name="firstName" labelName='First Name' value={this.state.form.firstName} isRequired={true} typeName='text-required'/>
          <Field name="lastName" labelName='Last Name'value={this.state.form.firstName} isRequired={true} typeName='text-required'/>
          {this.state.form.showEmail ? <Field name="email" labelName='Email' isRequired={true} value={this.state.form.email} typeName='email-required' isRequired='true'/> : null}
          {this.state.form.showBirthDate ? <Field name="bithDate" labelName='Birth Date' typeName='date' isRequired='true'/> : null}
          {this.state.form.showPhoneNumber ? <Field name="phoneNumber" labelName='Phone number' typeName='text' isRequired='true'/> : null}
          {this.state.form.showProgram  ? <Field name="program" name="proneNumber" labelName='Program' onSelect={this.selectProgram} options={this.state.form.programs} typeName='dropDown' isRequired='true'/> : null}
          {this.state.form.showProgram  ? <Field name="location" labelName='Location'options={this.state.form.locations} typeName='dropDown' isRequired='true'/> : null}
          {this.state.form.showEntryTerm  ? <Field name="entryTerm" labelName='Entry term' options={this.state.form.entries} typeName='dropDown' isRequired='true'/> : null}
          <button type="submit" onClick={this.sendLead} className="btn btn-default">Submit</button>
        </form>
      </div>
    );
  }
}

export default App;



