import axios from 'axios';
import UrlsConfig from '../../components/config/urls'

class formsService{

  api = "";

  constructor(){    
    let config = UrlsConfig.getUrls();
    this.api = config.api;
  }

  

  getForm(){      
      let url = this.api + "5199/forms/GetFormsById?id=2597";      
      return axios.get(url);
  }

  getPrograms(){    
    let url = this.api + "5199/programsofinterest/GetProgramsByForm?id=2597";      
    return axios.get(url);
  }

  getEntryTerms(){    
    let url = this.api + "5199/programs/GetEntryTerms?id=-1&Parent_Program=-1";      
    return axios.get(url);
  }

  getDomains(){    
    let url = this.api + "5199/forms/GetFormsDomainById?id=2597";      
    return axios.get(url);
  }

  getLocations(){    
    let url = this.api + "5199/location/GetLocationByProgram?id=c953215c-ba4e-e611-941a-005056912278&Parent_Program=1266";      
    return axios.get(url);
  }

  getFormInfo(){
    return Promise.all([
      this.getForm(), 
      this.getPrograms(),
      this.getEntryTerms(),
      this.getDomains(),
      this.getLocations()
    ]);
  }

  
}


export default new formsService


