import React, { Component } from 'react';
import emitter from '../../components/config/emitter';

class Field extends Component {

  constructor(props) {
      super(props);      
      this.handleChange = this.handleChange.bind(this);
      this.onkeyChange = this.onkeyChange.bind(this);
  }


  handleChange(e) {    
    var name = e.target.value;
    this.props.onSelect(name);
  }

  onkeyChange(e){     
     e.preventDefault();
     var value = e.target.value;
     emitter.emit('valueChanged', { name: this.props.name, value: value});    
  }

  render() {

    let field = null;
    let options = [];
    
    if(this.props.options !== undefined && this.props.typeName === "dropDown")
    {
        //<option value="volvo">Volvo</option>
         this.props.options.forEach(function(option) {
            options.push(
                <option value={option.value}>{option.label}</option>
            );
        });

        field =(
            <div className="form-group">
                <label htmlFor={this.props.labelName}>{this.props.labelName}</label>
                <select className="form-control" onChange={this.handleChange}>
                    {options}
                </select>
            </div>
        );
    }
    else{
        field = (
            <div className="form-group">
                    <label htmlFor={this.props.labelName}>{this.props.labelName}</label>
                    {this.props.isRequired? <input type={this.props.typeName} onKeyUp={this.onkeyChange} className="form-control" required/>: <input type={this.props.typeName} className="form-control" onKeyUp={this.onkeyChange}/>}            
            </div>
        );
    }
   
    
    return (field);
  }
}

export default Field;



