import axios from 'axios';
import UrlsConfig from '../../components/config/urls'

class postInformationService{

  api = "";
  url = "";
  urlXml = "";
  urlRequest = "";
   
  constructor(){
      let config = UrlsConfig.getUrls();
      this.api = config.api;
      this.url = config.requestManagerApi + 'Request';
      this.urlXml = config.requestManagerApi + 'RequestXML';
      this.urlRequest = config.request;
  }

   post(item, action, stateName){
        return new Promise((resolve, reject)=>{           
            axios.then(this.url + '?ActionName=' + action + '&StateName=' + stateName, item)
            .success((data) => {
                resolve(data.data);
            })
            .catch((data) => {
                reject(data);
            });
        });
    }

    postXml(item){
        return new Promise((resolve, reject)=>{            
            axios.post(this.urlXml, item)
            .then((data) => {
                resolve(data.data);
            })
            .catch((data) => {
                reject(data);
            });            
        })
      }


      postNoRoute(item, action, stateName){
         return new Promise((resolve, reject)=>{
            axios.post(this.url, item)
            .then((data) => {
                resolve(data.data);
            })
            .catch((data) => {
                reject(data);
            });            
          })
      }

      postWithRoute(item, action, stateName, ip, gceResponse){
         return new Promise((resolve, reject)=>{
            axios.post(this.url + '/validateRequest?ip=' + ip + '&gce=' + gceResponse, item )
            .then(function (data) {
                resolve(data.data);
            })
            .catch(function (data) {
                reject(data);
            });
         })
      }


      postWebRequest(requestAction, withValidation, ip, gceResponse){
        return new Promise((resolve, reject)=>{
            if(withValidation){
                this.postWithRoute({
                    "requestAction": {
                    "name": requestAction,
                    },
                    "requestState": {
                    "name": "Received",
                    },
                }, 'LMSFILE','Received', ip, gceResponse ).then((result) =>{
                    resolve(result.data);
                },(err) =>{
                    reject(err);
                })
                }
                else{
                this.postNoRoute({
                    "requestAction": {
                    "name": requestAction,
                    },
                    "requestState": {
                    "name": "Received",
                    },
                }, 'LMSFILE','Received').then((result)=>{
                    resolve(result);
                },(err)=>{
                    reject(err);
                })
            }
        })
      }

      postRequestLms(campaignId, fileOrFormId, requestId, isTest){
        return new Promise((resolve, reject)=>{
            this.postRequestLmsSchema({
                "requestStatusId": 1,
                "requestId": requestId,
                "campaignId": campaignId,
                "fileFormId": fileOrFormId,
                "dateCreated": "2016-06-16T02:55:57.176Z",
                "dateModified": "2016-06-16T02:55:57.176Z",
                "isTest": isTest
            }).then((result)=>{
                resolve(result.data);
            },(err)=>{
                reject(err);
            })
        })        
      }

    postRequestLmsSchema = function(item){
     return new Promise((resolve, reject)=>{
        axios.post(this.urlRequest + "/post", item)
        .then((data) => {
            resolve(data);
        })
        .catch(function (data) {
            reject(data);
        });
     })
    }

      postRequestXml(request, stringXml){
        return new Promise((resolve, reject)=>{
            this.postXml({
                request: request,
                xmlData: stringXml
            }).then((result)=>{
                resolve(result.data);
            },(err)=>{
                reject(err);
            })
        })
      }

      // 1 File, 2 Xml
      insertRequest(requestAction, campaignId, fileOrFormId, type, withValidation, stringXml, ip, gceResponse, isTest){
        return new Promise((resolve, reject)=>{
            this.postWebRequest(requestAction, withValidation, ip, gceResponse).then((resultRequest)=>{
            var id = resultRequest.id;          
            if(type == 1){
                this.postRequestLms(campaignId, fileOrFormId, id, isTest).then((resultRequestLms)=>{
                    resolve(resultRequestLms.data);
                })
            }
            else if(type == 2){
                this.postRequestXml(resultRequest, stringXml).then((resultRequestLms)=>{
                //After saving xml save request
                this.postRequestLms(campaignId, fileOrFormId, id, isTest).then((resultRequestLms)=>{
                    resolve(resultRequestLms.data);
                })
              })
            }

            },(err)=>{
                reject(err);
            })
        })
      }
}


export default new postInformationService();


