/*global X2JS*/
import _ from 'lodash';

class FormSchema{

    form = {};
    formInfo = {};
    x2js;
    idForm;
    isTest = false;

    constructor(){
        this.form = {
          firstName: '',
          lastName: '',
          email: '',
          birth_date : "",
          IP: '',
          majorName: "",
          major: -1,
          campusName: "Fort Lauderdale/Davie Campus",
          campus: '40DB4F59-8F9D-E411-9403-005056BE659B',
          origin_Source : -1,          
          entry: -1,
          status: -1,
          anticipatedEntryTermId: '',
          nsugr_weburl : '',
          phoneNumber : '',
          colleges : ''  
        }

        this.x2js = new X2JS();
    }

    //:id?/:idCampaign?/:idVendor?/:isTest?/:captcha?
    /*
        origin_Source : $routeParams.idVendor,          
        entry: $routeParams.idCampaign,
    */
    setParameterFromUrl(array){
        if(array && array.length > 0){
            _.each(array, (item, index)=>{
                if(index == 0){
                    this.idForm = item;
                }
                else if(index == 1){
                    this.form.origin_Source = item;
                }
                else if(index == 2){
                    this.form.entry = item;
                }
                else if(index == 3){
                    this.isTest = item;
                }
            })
        }
    }

    getSchema(){
        return this.form;
    }

    getAsXml(){
        let root = {};
        root.form = this.form;
        return this.x2js.json2xml_str(root);
    }

    getIdForm(){
        return this.idForm;
    }

    getIsTest(){
        return this.isTest;
    }

    setFormInfo(item){
        this.formInfo =  item;
    }

    getFormInfo(){
        return this.formInfo;
    }

    updateProperty(property, value){
        this.form[property] = value;
        return this.form;
    }
}

export default new FormSchema();


